import React, {useState, useEffect} from 'react';
import Tierinfo from '../Tierinfo';
import * as api from '../../../api/api';

const Body = ({leagueEntriesInfo}) => {
    console.log(leagueEntriesInfo)
    return(
        <div className="rank_wrapper">
            <Tierinfo name="솔로랭크" data={leagueEntriesInfo}/>
            <Tierinfo name="자유랭크" data={leagueEntriesInfo}/>
            <Tierinfo name="롤토체스" data={leagueEntriesInfo}/>
            <style jsx>
                {`
                    .rank_wrapper {
                        padding-top: 40px;
                        width: 300px;
                    }
                `}
            </style>
        </div>
    );
};

export default Body;